import React from 'react';
import { graphql } from 'gatsby';
import { AnyImage, SEO, HeroImage, About, PersonalProjects } from 'src/components';
import { baseStyle } from 'src/styles';
import AboutMDX from 'src/internal/about.mdx';

import styled from '@emotion/styled';

type Props = {
  className?: string;
};

const Component: React.FC<Props> = ({ className }) => (
  <main className={className}>
    {/* <AnyImage filename=?'home-hero-image.jpg' /> */}
    <HeroImage filename={`home-hero-image.jpg`} />
    <About />
    <PersonalProjects />
  </main>
);

const StyledComponent = styled(Component)`
  ${baseStyle}
  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

export default ({ path }: { path: string }) => (
  <>
    <SEO title='Top' pathname={path} />
    <StyledComponent />
  </>
);

export const query = graphql`
  query {
    allMdx {
      nodes {
        fields {
          collection
        }
      }
    }
  }
`;
